.contact-preview {
    width: 100%;
    display: flex;
    border: 1px solid $clr5;
    background-color: $clr2;
    padding: rem(10px);
    border-radius: rem(10px);
    align-items: center;

    img {
        width: rem(80px);
        height: rem(80px);
        object-fit: cover;
        border-radius: 50%;
    }
    p {
        font-weight: 600;
        margin: 0 auto;    }
}
