.rates-list {
    text-align: center;
    padding: rem(10px);
    background-color: $clr2;
    border-radius: rem(10px);
    border: 1px solid black;
    h2 {
        margin-bottom: rem(10px);
        font-size: 3.75rem;
        font-weight: 300;
    }

    .currency-list {
        display: flex;
        justify-content: space-evenly;
        .usd-rate,
        .eur-rate,
        .gbp-rate,
        .cny-rate {
            display: grid;
            grid-template-rows: 1fr 1fr 1fr;
            height: rem(150px);
            width: rem(200px);
            align-items: center;
            img {
                width: auto;
                justify-self: center;
            }
        }
    }
    @include for-mobile-layout {
        padding: rem(5px);

        h2 {
            font-size: smaller;
            font-weight: 600;
        }

        .currency-list {
            .usd-rate,
            .eur-rate,
            .gbp-rate,
            .cny-rate {
                img {
                    width: 30px;
                }
                align-items: center;
                height: rem(80px);
                width: rem(80px);
                h4 {
                    color: #258734;
                }
            }
        }
    }
}
