.transfers-table {
    .MuiPaper-root {
        background-color: $clr2;
        border-radius: rem(10px);
        border: 1px solid black;
        padding: rem(10px);
        text-align: center;
    }
    h2 {
        font-family: inherit;
        line-height: inherit;
        margin-bottom: 0.625rem;
    }

    .amount-plus {
        color: #258734;
        font-weight: 600;
    }
    .amount-minus {
        color: red;
    }

    @include for-mobile-layout {
        width: 100%;
        .MuiPaper-root {
            padding: rem(5px);
        }

        .MuiTableCell-root {
            min-width: 0px;
        }
        h2 {
            font-size: smaller;
            font-weight: 600;
        }

        .MuiTable-root {
            min-width: auto;
        }
        margin-inline-start: 0;
        margin-inline-end: 0;
        .MuiTableCell-root {
            padding: rem(2px);
            font-size: rem(10px);
        }
    }
}
